/**
 * commonly used functions
 * Add a level of indirection to lodash so we can replace
 * it if desired, while keeping function names the same
 */
// TODO: Import individual packages vs referencing all of lodash
import {
  cloneDeep,
  filter,
  debounce,
  findIndex,
  isEmpty,
  isString,
  isEqual,
  noop,
  omit,
  orderBy,
  pull,
  sumBy,
  maxBy,
  meanBy,
  sortBy,
  transform,
  isFunction,
  uniq,
  uniqBy,
  groupBy,
  property,
  get,
  set,
  range,
  parseInt,
  padStart,
  isNaN,
  values,
  throttle,
  pick,
  pickBy,
  differenceBy,
  differenceWith,
  isEqualWith,
  curry,
  isObject,
  isBoolean,
} from 'lodash';

import { compose } from './compose';

/**
 * Concatenates classNames from arguments
 *
 * @param {...any} classnames
 */
export const cx = (...classnames) =>
  classnames.reduce((result, cn) => `${result} ${cn || ''}`, '');

// if entry exists, remove it, if not, add it
const toggleInObject = (obj, key, value) =>
  obj[key] !== undefined ? omit(obj, [key]) : { ...obj, [key]: value };
const toggleInArray = (array, value) =>
  array.indexOf(value) === -1 ? [value] : [];

const arrayMoveMutate = (array, from, to) => {
  array.splice(to < 0 ? array.length + to : to, 0, array.splice(from, 1)[0]);
};

const arrayMove = (array, from, to) => {
  array = array.slice();
  arrayMoveMutate(array, from, to);
  return array;
};

const accessorDefault = (item) => item;
const addOrRemove = (array, item, accessor = accessorDefault) => {
  const index = array.findIndex((iItem) => accessor(iItem) === accessor(item));

  if (index !== -1) {
    const nextArray = cloneDeep(array);
    nextArray.splice(index, 1);
    return nextArray;
  }

  return [...array, item];
};

export function runAll(...fns) {
  return (...args) =>
    fns.forEach((fn) => {
      if (Array.isArray(fn)) runAll(fn);
      if (isFunction(fn)) fn(...args);
    });
}

export function hashArray(arr, fieldName) {
  return arr.reduce(
    (result, item) => ({
      ...result,
      [fieldName ? item[fieldName] : item]: true,
    }),
    {},
  );
}

export function safeCall(fun, ...args) {
  if (isFunction(fun)) {
    fun(...args);
  }
}

export const removeUndefinedProperties = (obj) => {
  const cleanObject = { ...obj };
  Object.keys(obj).forEach(
    (key) => cleanObject[key] === undefined && delete cleanObject[key],
  );
  return cleanObject;
};

export default {};
