import React from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';

const blockName = 'separator';

const MARGIN_SIZE = {
  S: 's',
  M: 'm',
  L: 'l',
  none: 'none',
};

const POSITION = {
  INITIAL: 'initial',
  CENTER: 'center',
  END: 'end',
};

const SeparatorWrapper = aggregatedClasses('div')(
  'separator-wrapper',
  null,
  ({ margin, orientation, dashed, position }) => ({
    dashed,
    [margin]: margin,
    [orientation]: orientation,
    [position]: position,
  }),
);

const Separator = aggregatedClasses('div')(
  blockName,
  null,
  ({ dashed, margin, orientation, type }) => ({
    dashed,
    [margin]: margin,
    [orientation]: orientation,
    [type]: type,
  }),
);

const types = ['category-level', 'group-level', 'non-form'];

const DSSeparator = ({
  containerProps = {},
  dashed = false,
  margin = MARGIN_SIZE.S,
  orientation = 'horizontal',
  type = 'category-level',
  position = POSITION.INITIAL,
  className,
  style,
}) => (
  <SeparatorWrapper
    {...containerProps}
    className={className}
    classProps={{
      margin,
      orientation,
      dashed,
      position,
    }}
    style={style}
  >
    <Separator
      classProps={{
        dashed,
        margin,
        orientation,
        type,
      }}
    />
  </SeparatorWrapper>
);

DSSeparator.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * If the separator has to be dashed or solid
   */
  dashed: PropTypes.bool,
  /**
   * Amount of margin to be added to the separator
   */
  margin: PropTypes.oneOf(['s', 'm', 'l', 'none']),
  /**
   * Orientation 'horizontal' or 'vertical'
   */
  orientation: PropTypes.string,
  /**
   * ['category-level', 'group-level', 'non-form']
   */
  type: PropTypes.oneOf(types),
  /**
   * ['initial', 'center', 'end']
   */
  position: PropTypes.oneOf(['initial', 'center', 'end']),
};

export default DSSeparator;
